.movie-app > .row {
	overflow-x: auto;
	flex-wrap: nowrap;
}

.overlay{
	position: absolute;
	background: rgba(20, 0, 0, 0.8);
	width: 100%; 
	transition: 0.5s ease;
	opacity: 0;
	bottom: 0;
	font-size: 20px;
	padding: 20px;
	text-align: center;
	border:1px red solid
}
.overlay-shown :hover {
	cursor: pointer;
	opacity:1
	
}
